#skills{
    overflow: hidden;
    width :100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;

}


.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
    color: yellow;
}

.skillDesc{
    font-size: 1rem;
    font-weight: 400;
    max-width:50rem;
    padding:0 2rem;

}

.skillBars{
    margin:2rem;
    width:100vw;
    max-width: 80%;
    text-align: left;
}

.skillBar{
    display: flex;
    margin:1rem;
    padding:1.5rem 3rem;
    border-radius:0.5rem;
    background : rgb(50,50,50);


}

.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}



.skillBarText>p{
    font-size: 0.9rem;
    font-weight: 200;
}